<template>
	<view class="publish">
		<view class="content"><textarea v-model="form.msg" auto-height="true" value="" placeholder="请输入你的留言"></textarea></view>
		<view class="media row">
			<block v-for="(item, index) in image_files" :key="index">
				<view class="box">
					<text class="delete" @tap="delect(index)"></text>
					<image mode="aspectFill" :src="item.url" :data-src="item.url" @tap="previewImage"></image>
				</view>
			</block>
			<view class="box" v-if="video_files.length > 0"><video :src="video_files[0].url"></video></view>
			<view class="box default" v-if="flag" @click="chooseVideoImage"></view>
		</view>
		<view class="btn" @click="submit">立即发表</view>
	</view>
</template>

<script>
import { API } from '@/util/api.js';

export default {
	data() {
		return {
			aid: '',
			msg: '',
			image_files: [],
			video_files: [],
			flag: true,
			itemList: ['图片', '视频']
		};
	},
	onLoad(options) {
		this.aid = options.aid;
	},
	methods: {
		delect(index) {
			uni.showModal({
				title: '提示',
				content: '是否要删除该图片',
				success: res => {
					if (res.confirm) {
						this.image_files.splice(index, 1);
					}
				}
			});
		},
		previewImage: function(e) {
			var current = e.target.dataset.src;
			uni.previewImage({
				current: current,
				urls: this.image_files
			});
		},
		chooseVideoImage() {
			uni.showActionSheet({
				title: '选择上传类型',
				itemList: this.itemList,
				success: res => {
					if (res.tapIndex == 0) {
						this.chooseImages();
					} else {
						this.chooseVideo();
					}
				}
			});
		},
		chooseImages() {
			uni.chooseImage({
				count: 1,
				sizeType: ['original', 'compressed'],
				sourceType: ['album', 'camera'],
				success: res => {
					uni.showLoading();
					let igmFile = res.tempFilePaths;
					uni.uploadFile({
						url: API.uploadPicture,
						filePath: igmFile[0],
						name: 'file',
						success: res => {
							uni.hideLoading();
							let data = JSON.parse(res.data);
							console.log(data);

							if (data.code === 10000) {
								this.image_files = this.image_files.concat({
									url: data.data.url,
									fileType: data.data.fileType
								});
								if (this.image_files.length >= 6) {
									this.flag = false;
								} else {
									this.flag = true;
								}
							} else {
								uni.showToast({
									title: data.msg,
									icon: 'none',
									duration: 2000
								});
							}
						}
					});
				}
			});
		},
		chooseVideo() {
			uni.chooseVideo({
				maxDuration: 15,
				count: 1,
				sourceType: ['camera', 'album'],
				success: responent => {
					uni.showLoading();
					let videoFile = responent.tempFilePath;
					uni.uploadFile({
						url: API.uploadPicture,
						filePath: videoFile,
						name: 'file',
						success: res => {
							uni.hideLoading();
							let data = JSON.parse(res.data);
							if (data.code === 10000) {
								this.itemList.splice(1, 1);
								this.video_files.push({
									url: data.data.url,
									fileType: data.data.fileType
								});
								if (this.image_files.length >= 6) {
									this.flag = false;
								} else {
									this.flag = true;
								}
							} else {
								uni.showToast({
									title: data.msg,
									icon: 'none',
									duration: 2000
								});
							}
						}
					});
				}
			});
		},
		submit() {
			if (!this.msg) {
				uni.showToast({
					title: '请输入留言内容',
					icon: 'none',
					duration: 2000
				});
			}

			this.request(API.commentAdd,'POST',{aid:this.aid,msg:this.msg,image_files:this.image_files,video_files:this.video_files}).then(res => {
				if (res.code === 10000) {
					uni.showToast({
						title: '发表成功',
						duration: 2000
					});
					setTimeout(()=>{
						uni.navigateTo({
							url:'/pages/message/messagList?aid='+this.aid
						})
					},2000);
					
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none',
						duration: 2000
					});
				}
			}, true);
		}
	}
};
</script>

<style lang="scss">
@import '../../assets/css/message/publish.scss'; 
</style>
